.home{
    position: relative;
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    overflow-x: hidden;
}

.cylinder-image-wrapper{
    width: 3em;
    height: 3em;
    border: 1px solid white;
    border-radius: 50%;
    background: rgba(0, 0, 0, 0.427);
    user-select: none;
    display: flex;
    align-items: center;
    justify-content: center;
}
.cylinder-image-wrapper p{
    color: white;
    font-size: 0.7rem;
}

.home-text{
    width: 25%;
    position: fixed;
    left: 4em;
    bottom: 4em;
    z-index: 2;
    display: flex;
    flex-direction: column;
    gap: 1em;
    user-select: none;
}
.home-text h1{
    font-size: 2rem;
    margin: 0;
    text-align: left;
    line-height: 0.8em;
    color: white;
}
.home-text p{
    text-align: left;
    color: white;
}
.home-text span{
    color: var(--orange);
}
.home-text button{
    width: max-content;
    border: none;
    border-radius: var(--radius);
    color: var(--orange);
    background: transparent;
    font-size: 1.2rem;
    cursor: pointer;
    display: flex;
    align-items: center;
    gap: 0.5em;
    transition: all 0.3s;
}
.home-text button:hover{
    background: var(--blue-dark);
    color: var(--orange);
}


.hero{
    position: relative;
    width: 100%;
    height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    gap: 1em;
    background: transparent;
    overflow: hidden;
}
.hero-video{
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
}
.hero-video video{
    width: 100%;
    height: 100%;
    object-fit: cover;
}
.hero-content{
    position: relative;
    z-index: 2;
    width: 100%;
    height: 100%;
    padding: 4em 8em;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    gap: 1em;
}
.hero-content > div{
    display: flex;
    align-items: center;
    gap: 1em;
}
.hero-play-btn{
    color: var(--blue);
    background: transparent;
    border: none;
    display: flex;
    align-items: center;
    gap: 0.5em;
    font-size: 1rem;
}
.hero h1{
    position: relative;
    font-size: 5rem;
    font-weight: 500;
    line-height: 0.9em;
    color: var(--font-clr);
}
.hero h1 span{
    font-style: italic;
    font-weight: 800;
    color: var(--font-clr);
}
.hero p{
    width: 50%;
    color: var(--font-light);
    font-size: 1rem;
    line-height: 1.5em;
}
 span{
    color: var(--blue-dark);
    font-weight: 600;
 }
