.hero-btn{
    position: absolute;
    bottom: 0;
    right: 5em;
    width: 10em;
    height: 10em;
    border-radius: 50%;
    background: var(--orange);
    display: flex;
    align-items: center;
    justify-content: center;
}

.home-sections{
    width: 100%;
    padding: 4em;
    display: flex;
    flex-direction: column;
    gap: 2em;
    background: white;
}
.home-sections h1{
    color: var(--blue-dark);
    font-size: 5.5rem;
    font-weight: 600;
    transform: translateY(100px);
    opacity: 0;
}
.home-sections p{
    text-transform: uppercase;
    width: 50%;
    color: var(--blue-dark);
    margin-bottom: 1em;
    transform: translateY(100px);
    opacity: 0;
}
.section-1, .section-2, .section-3{
    width: 100%;
    height: 100vh;
    background: black;
    background-size: cover;
}
