/* Info */
.info{
    width: 100%;
    height: 100vh;
    display: flex;
    flex-direction: column;
    gap: 1em;
    justify-content: center;
    padding: 4em 8em;
}
.info h1{
    white-space: nowrap;
    line-height: 1em;
    color: var(--font-clr);
}
#info-do{
    color: black;
    font-weight: 700;
    font-style: italic;
}
#info-dont{
    color: var(--font-light);
    font-weight: 300;
}
.info p{
    color: var(--font-light);
    width: 50%;
    line-height: 1.5em;
    align-self: flex-end;
}
.info p span{
    color: var(--blue-dark);
    text-decoration: underline;
}



.view-work{
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 4em 8em;
}

.services-wrapper{
    padding: 4em 8em;
}
.services-wrapper p{
    color: var(--font-light);
}


.marquee-wrapper{
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    background: #F6F3EE;
    padding: 4em 0;
    gap: 4em;
}
.marqee-overlay-yellow{
    background: var(--orange);
    width: 100%;
    height: 50vh;
    transform-origin: top;
}

.marquee-wrapper span{
    font-weight: 600;
    text-transform: uppercase;
    font-size: 8rem;
    color: transparent;
    -webkit-text-stroke-width: 2px;
    -webkit-text-stroke-color: var(--blue-dark);
}
.marquee-wrapper span:nth-child(2){
    font-style: italic;
}


/* MEDIA QUERIES */
@media only screen and (max-width: 980px){
    .hero > div{
        justify-content: flex-end;
        padding:  2em 4em;
    }
    .hero h1{
        font-size: 4rem;
    }
    .hero p{
        width: 100%;
    }

    .info, .pre-projects{
        padding: 2em 4em;
        justify-content: flex-end;
    }
    .info h1{
        font-size: 3rem;
        line-height: 1em;
        white-space: wrap;
    }
    .secondaty-titles{
        font-size: 3rem;
        line-height: 1em;
    }
    .info p{
        width: 100%;
    }

    .services-wrapper{
        padding: 2em 4em;
    }

    .marquee-wrapper span{
        font-size: 5rem;
    }
}

@media only screen and (max-width: 480px){
    .hero > div{
        justify-content: flex-end;
        padding: 2em 1em;
    }
    .hero h1{
        font-size: 4rem;
    }
    .hero p{
        width: 100%;
    }

    .info, .pre-projects{
        padding: 2em 1em;
        justify-content: flex-end;
    }
    .info h1{
        font-size: 3rem;
        line-height: 1em;
        white-space: wrap;
    }
    .secondaty-titles{
        font-size: 3rem;
        line-height: 1em;
    }
    .info p{
        width: 100%;
    }

    .services-wrapper{
        padding: 1em;
    }

    .marquee-wrapper span{
        font-size: 5rem;
    }
}

