.services{
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    gap: 2em;
}

.service-wrapper{
    flex: 1 1 calc(50% - 1em);
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 2em;
    padding: 4em;
    background: white;
    border-radius: 35px;
}
.service-wrapper:nth-child(1){
    border-top: 5px solid var(--prpl);
}
.service-wrapper:nth-child(2){
    border-top: 5px solid var(--orng);
}
.service-wrapper:nth-child(3){
    border-top: 5px solid var(--grn);
}
.service-wrapper:nth-child(4){
    border-top: 5px solid var(--bl);
}
.service-img{
    width: 50%;
    overflow: hidden;
}
.service-img img{
    width: 100%;
    height: 100%;
    object-fit: contain;
    margin: auto;
}
.service-content{
    display: flex;
    flex-direction: column;
    gap: 1em;
}
.service-content p{
    font-size: 2.5rem;
    font-weight: 500;
    color: var(--font-clr);
}
.service-content small{
    line-height: 1.5em;
    font-size: 0.9rem;
}


@media only screen and (max-width: 480px){
    .service-wrapper{
        flex-direction: column;
        padding: 2em;
        flex-basis: calc(100% - 1em); /* Take up full width with gap */
    }
    .service-img, .service-content{
        width: 100%;
    }
    .service-content p{
        font-size: 2rem;
    }
}

